<template>
  <div class="column is-4">
            <div class="field">
              <label for="" class="label">Title</label>
              <div class="control has-icons-left">
                <input type="text" placeholder="" class="input" v-model="title" required>
                <span class="icon is-small is-left">
                  <i class="ri-edit-line"></i>
                </span>
              </div>
            </div>

            <div class="field">
              <label for="" class="label">Subtitle</label>
              <div class="control has-icons-left">
                <input type="text" placeholder="" class="input" v-model="subtitle" required>
                <span class="icon is-small is-left">
                  <i class="ri-edit-line"></i>
                </span>
              </div>
            </div>

              <button @click="submit" class="button">
                Submit
              </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
        title:'',
        subtitle:''
    }
  },

  methods: {
    submit() {
        this.$axios.post('/admin/sitesettings',{
          title:this.title,
          subtitle:this.subtitle,
        }).then(response=>{
          if (response.data.status === 200 ) {
              this.$notify('Done.')
          } else {
              this.$notify('Error: '+response.data.msg)
          }
        })
    }
  }

}
</script>